@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
  background-color: black;
  color: white;
  font-size: 1rem;
  padding: 1rem;
  display: flex;
  justify-content: center;
}

.notconnected {
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
}

.connbtn {
  border-radius: 4px;
  background-color: #01b600;
  width: 142px;
  height: 43px;
  color: white;
  border-radius: 8px;
  border: none;
  font-size: 1rem;
}

.nonet {
  background-color: blue;
}

.connbtndiv {
  display: flex;
  justify-content: center;
}

.connected {
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.netinfo {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 1rem;
}

.lblkey {
  justify-self: end;
  padding-right: 1rem;
}

.lblval {
  padding: 0.1rem;
  padding-right: 1rem;
  width: 80%;
  border-radius: 4px;
}

.bckbtndiv {
  display: flex;
  justify-content: center;
  padding: 4rem;
  padding-left: 8rem;
}

.bckbtn {
  width: 100%;
  height: 43px;
  border-radius: 4px;
  background-color: #01b600;
  border: none;
  color: white;
}

.networkshead {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 2rem;
  background-color: black;
  padding: 1rem;
}

.netlabel {
  font-size: 1rem;
  padding: 1rem;
  font-size: 1.2rem;
}

hr {
  border-bottom: 1px solid #1f1209;
  box-shadow: 0 2px 2px -2px #333;
}

.netconnectbtn {
  height: 43px;
  width: 40%;
  border-radius: 4px;
  background-color: #01b600;
  border: none;
  color: white;
}

.netconnectbtndiv {
  display: flex;
  justify-content: space-evenly;
  padding: 2rem;
}

.netconnectbackbtn {
  height: 43px;
  width: 40%;
  border-radius: 4px;
  background-color: red;
  border: none;
  color: white;
}

.nointlogo {
  display: flex;
  justify-content: center;
  padding: 2rem;
}

.ppp {
  background-color: #baf8ba;
  height: 100vh;
}

.popup {
    z-index: 999;
    background-color: transparent;
    position: absolute;
    top:0;
    height:30vh;
    width: 100vw;
}

.apop {
    margin: 0 auto;
    width: 90vw;
    height: 100%;
    background-color: blue;
    padding-top: 2rem;
    display: grid;
    grid-template-rows: repeat(2,1fr);
    justify-content: center;
    align-items: center;
}

.wifibtns {
    display: grid;
   justify-content: center;
   grid-template-columns: 1fr 1fr;

}


.inputbox {
    display: grid
}

.wifiinput {
    margin-top:1rem
}
.l-c {
  min-height: 100vh;
  display: grid;
  justify-content: center;
}

.l-m {
  width: 522px;
  background-color: white;
  box-shadow: 0 0 30px #d8d8d8;
}

.l-head {
  background-color: black;
  font-family: Roboto;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  box-shadow: 0 2px 6px 0 rgba(93, 148, 213, 0.4);
  position: fixed;
  width: 522px;
  /* font-weight: bold; */
}

.l-body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 3rem;
}

.l-inpd {
  margin-top: 7rem;
  /* display: flex; */
  justify-content: center;
  border-bottom: 1px solid lightblue;
}

.l-inpd2 {
  margin-top: 3rem;
  /* display: flex; */
  justify-content: center;
  border-bottom: 1px solid lightblue;
  width: 100%;
}

.l-inp {
  border: none;
  outline: none;
  padding-bottom: 0.5rem;
  font-size: 1rem;
  width: 100%;
  font-family: Roboto;
}

.login-btndiv {
  display: flex;
  justify-content: center;
  padding-top: 3rem;
}

.login-btn {
  color: #444444;
  background: #f3f3f3;
  border: 1px #dadada solid;
  padding: 5px 10px;
  border-radius: 2px;
  font-weight: bold;
  font-size: 9pt;
  outline: none;
  width: 150px;
  height: 30px;
}

.login-btn:hover {
  border: 1px #c6c6c6 solid;
  box-shadow: 1px 1px 1px #eaeaea;
  color: #333333;
  background: #f7f7f7;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .l-m {
    width: 100vw;
  }
  .l-head {
    width: 100vw;
  }
}

