@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");

.l-c {
  min-height: 100vh;
  display: grid;
  justify-content: center;
}

.l-m {
  width: 522px;
  background-color: white;
  box-shadow: 0 0 30px #d8d8d8;
}

.l-head {
  background-color: black;
  font-family: Roboto;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  box-shadow: 0 2px 6px 0 rgba(93, 148, 213, 0.4);
  position: fixed;
  width: 522px;
  /* font-weight: bold; */
}

.l-body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 3rem;
}

.l-inpd {
  margin-top: 7rem;
  /* display: flex; */
  justify-content: center;
  border-bottom: 1px solid lightblue;
}

.l-inpd2 {
  margin-top: 3rem;
  /* display: flex; */
  justify-content: center;
  border-bottom: 1px solid lightblue;
  width: 100%;
}

.l-inp {
  border: none;
  outline: none;
  padding-bottom: 0.5rem;
  font-size: 1rem;
  width: 100%;
  font-family: Roboto;
}

.login-btndiv {
  display: flex;
  justify-content: center;
  padding-top: 3rem;
}

.login-btn {
  color: #444444;
  background: #f3f3f3;
  border: 1px #dadada solid;
  padding: 5px 10px;
  border-radius: 2px;
  font-weight: bold;
  font-size: 9pt;
  outline: none;
  width: 150px;
  height: 30px;
}

.login-btn:hover {
  border: 1px #c6c6c6 solid;
  box-shadow: 1px 1px 1px #eaeaea;
  color: #333333;
  background: #f7f7f7;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .l-m {
    width: 100vw;
  }
  .l-head {
    width: 100vw;
  }
}
